.login{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login img{
    margin: 30px;
    object-fit: contain;
    width: 100px;
}
.login__container{
    width: 350px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px 20px;
    align-items: center;
}
.login__container h1{
    font-size: 25px;
    margin: 20px 0;
}
.login__container form{
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}
.login__container form div{
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.login__container form input{
    margin-bottom: 10px;
    padding: 8px 16px;
    background-color: white;
    width: 100%;
    border: none;
    border: 1px solid gray;
    border-radius: 3px;
}
.login__container form input:focus{
    outline: 2px solid skyblue;
}
.login__signInButton{
    border: none;
    background-color: #f0c14b;
    border-radius: 5px;
    height: 30px;
    width: 100%;
    cursor: pointer;
}
.login__signInButton:hover{
    background-color: var(--primary-shade);
}
.login__container p{
    padding: 10px 0;
    font-size: 14px;


}
.login__registerButton{
    border: none;
    /* background-color: #f0c14b; */
    border-radius: 5px;
    height: 30px;
    width: 100%;
    cursor: pointer;
}
.login__registerButton:hover{
    background-color: lightgray;
}

